import { Component } from 'react'
// import HeroImg from '../assets/img/illustration-intro.png'
import { ReactComponent as Logo } from "../assets/img/logo.svg"





export class AppHero extends Component {

    render() {

        return (
            <div className="app-hero">
                <div className="logo container"><Logo/></div>
                <div className="content container">
                    <h2>Exquisite dining <br /> since 1989</h2>
                    <p>Experience our seasonal menu in beautiful country <br /> surroundings. Eat the freshest produce from the comfort <br /> of our farmhouse.</p>
                    <div className="btn-holder"><button className="btn2">book a table</button></div>
                    
                </div>

            </div>
        )
    }
}