import { Component } from 'react'

import { ReactComponent as PatternOne } from "../assets/img/patterns/pattern-curve-top-right.svg"
import { ReactComponent as PatternTwo } from "../assets/img/patterns/pattern-curve-top-left.svg"
import { ReactComponent as PatternThree } from "../assets/img/patterns/pattern-lines.svg"

import { ReactComponent as Icon1 } from "../assets/img/patterns/pattern-divide.svg"





export class AppInfo extends Component {

    render() {

        return (
            <div>

                <div className="app-info container">

                    <div className="contant-one flex justify-between">
                        <div className="image">
                            <div className="pattern"><PatternOne /></div>
                        </div>

                        <div className="box">
                            <div className="icon"><Icon1 /></div>
                            <h1>Enjoyable place <br /> for all the family</h1>
                            <p>Our relaxed surroundings make dining with us a great <br /> experience for everyone. We can even arrange a tour of <br /> the farm before your meal.</p>
                        </div>
                    </div>

                    <div className="contant-two flex justify-between">
                        <div className="box">
                            <div className="icon"><Icon1 /></div>
                            <h1>The most locally sourced <br /> food</h1>
                            <p>All our ingredients come directly from our farm or local <br /> fishery. So you can be sure that you’re eating the freshest, <br /> most sustainable food.</p>
                        </div>

                        <div className="image">
                            <div className="pattern"><PatternTwo /></div>
                            <div className="pattern-three"><PatternThree /></div>
                        </div>
                    </div>


                </div>

            </div>
        )
    }
}
