import { Component } from 'react'

import { ReactComponent as PatternOne } from "../assets/img/patterns/pattern-curve-top-right.svg"
import { ReactComponent as PatternTwo } from "../assets/img/patterns/pattern-lines.svg"



export class AppFamily extends Component {

    render() {

        return (
            <div className="app-family container ">

                <div className="image-holder ">
                    <div className="image">
                        <div className="pattern-one"><PatternOne /></div>
                        <div className="pattern-two"><PatternTwo /></div>
                    </div>
                </div>
                <div className="content ">
                    <h1>Family Gathering</h1>
                    <p>We love catering for entire families. So please bring <br /> everyone along for a special meal with your loved ones. <br /> We’ll provide a memorable experience for all.</p>
                    <div className="btn-holder">
                        <button className="btn1">book a table</button>

                    </div>
                    <div className="info-txt">
                        <div className="txt1">FAMILY GATHERING</div>
                        <div className="txt2">SPECIAL EVENTS</div>
                        <div className="txt3">SOCIAL EVENTS</div>
                    </div>

                </div>


            </div>
        )
    }
}