import { Component } from 'react'
import { connect } from 'react-redux'
import { Link, withRouter } from 'react-router-dom'
import { toggleMenu } from '../store/actions/appSettingsActions'




export class _AppHeader extends Component {

    state = {
        isResized:false
    }

    componentDidMount() {
        window.addEventListener('resize', this.handleResize)
        this.handleResize()
    }
    
    setMenu = () => {
        if(this.state.isResized){
            this.props.toggleMenu()
        }
    }

    handleResize = () => {
        if (window.innerWidth < 720) {
            this.setState({
                isResized:true
            })
        }else{
            this.setState({
                isResized:false
            })

        }

    }



    render() {
        return (
            <div>
                <div className={`appHeader-screen ${this.props.isMenuOpen ? 'show-screen' : ''}`} onClick={this.setMenu}></div>
                <nav className="appHeader-nav-color" >
                    <div className="appHeader-nav flex justify-between container">
                        <h1 className="flex align-center justify-center">Proj</h1>
                        <ul className={`appHeader-nav-links flex ${this.props.isMenuOpen ? 'open-nav-links' : ''}`}>
                            <li onClick={this.setMenu}><Link to="/">Home</Link></li>
                            <li onClick={this.setMenu}><Link to="/geogram">Geogram</Link></li>
                            <li onClick={this.setMenu}><Link to="/map">Map</Link></li>
                        </ul>
                        <button className={`appHeader-menu-btn `} onClick={this.setMenu}  >☰</button>
                    </div>
                </nav>

            </div>
        )
    }

}






const mapStateToProps = (state) => {
    return {
        isMenuOpen: state.appSettingsModule.isMenuOpen
    }
}


const mapDispatchToProps = {
    toggleMenu
}

export const AppHeader = connect(mapStateToProps, mapDispatchToProps)(withRouter(_AppHeader))