import { ReactComponent as Icon1 } from "../assets/img/patterns/pattern-divide2.svg"


export function ItemPreview(props) {
    const { item } = props

    return (
        <article className="item-preview ">
            <div className="image">
                <div className="icon-two"><Icon1 /></div>
                <img src={item.imgUrl} alt="" />
            </div>

            <div>
                <h1>{item.title}</h1>
                <div className="txt">
                    <p>{item.desc1}</p>
                    <p>{item.desc2}</p>
                </div>
            </div>
        </article>
    )
}