import { Component } from 'react'
import { connect } from 'react-redux'

import { loadItems } from '../store/actions/itemActions'

import { ItemList } from '../cmps/ItemList'

import { ReactComponent as Icon1 } from "../assets/img/patterns/pattern-divide.svg"







export class _AppHL extends Component {

  componentDidMount() {
    this.props.loadItems()

  }


  render() {
    const items = this.props.items


    return (
      <div className="app-hl">
        <div className="content ">

          <div className="content-box  container ">

            <div className="icon "><Icon1 /></div>

            <div className="box-container ">
              <div className="box1">
                <h1>A few highlights from our <br /> menu</h1>
                <p>We cater for all dietary requirements, but here’s a glimpse <br /> at some of our diner’s favourites. Our menu is revamped <br /> every season.</p>
              </div>

              <div className="box2">
                <ItemList items={items} />
              </div>
            </div>


          </div>


        </div>



      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    items: state.itemModule.items
  }
}


const mapDispatchToProps = {
  loadItems,
}

export const AppHL = connect(mapStateToProps, mapDispatchToProps)(_AppHL)